.left-side{border-right:1px solid #ededed;display:flex;flex-direction:column}.left-side .top{display:flex;flex-direction:column;row-gap:50px;padding:10px;border-bottom:2px solid #ededed;background:#f5f5f5}.left-side .top .user__img{display:block;width:50px;height:50px;background:#9cd19d;border-radius:50%;overflow:hidden}.left-side .top .user__img img{width:100%}.left-side .top .search{position:relative;color:#6f6f6f}.left-side .top .search::before{content:"ðŸ”Žï¸Ž";position:absolute;left:7px;top:50%;transform:translateY(-50%)}.left-side .top .search input{width:100%;height:30px;border-radius:15px;color:inherit;padding:0 10px 0 30px}.left-side .top .search input:focus{outline:1px solid #60b7e3}.left-side .top .search input::placeholder{color:#c7c0c0}.left-side .bottom{overflow-y:auto}.left-side .bottom .title{font-size:1.5em;color:#60b7e3;margin:50px 0 50px 10px}.left-side .bottom .contact{display:grid;grid-template-areas:"img  name  time" "img  msg   .";grid-template-columns:50px auto 80px;column-gap:10px;align-items:center;padding:10px;border-bottom:1px solid #ededed;cursor:pointer}.left-side .bottom .contact:hover{background:#ededed}.left-side .bottom .contact:last-child{border-bottom:none}.left-side .bottom .contact__img{grid-area:img;width:50px;height:50px;border-radius:50%;overflow:hidden}.left-side .bottom .contact__img img{width:100%}.left-side .bottom .contact__name{grid-area:name;font-weight:bold}.left-side .bottom .contact__message{grid-area:msg;color:#6f6f6f;text-overflow:ellipsis;overflow:hidden;white-space:nowrap}.left-side .bottom .contact__time{grid-area:time;font-size:.8em;color:#959595;justify-self:flex-end}.left-side .bottom .no-results{padding-left:10px;font-size:1.2em;color:#6f6f6f;overflow:hidden}@media screen and (max-width: 768px){.left-side{transition:.5s}.left-side.slide-left{transform:translateX(-100%)}}